<template>
  <div>
    <van-nav-bar
      title="标题"
      left-arrow
      @click-left="$router.go(-1)"
    ></van-nav-bar>

    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input
            v-model="mobile"
            class="inp"
            maxlength="11"
            placeholder="请输入手机号码"
            type="text"
          />
        </div>
        <div class="form-item">
          <input
            v-model="picCode"
            class="inp"
            maxlength="5"
            placeholder="请输入图形验证码"
            type="text"
          />
          <img v-show="picUrl" :src="picUrl" @click="getCode" alt="" />
        </div>
        <div class="form-item">
          <input
            v-model="code"
            class="inp"
            placeholder="请输入短信验证码"
            type="text"
          />
          <button @click="getload">
            {{ count === 5 ? "获取验证码" : count + "秒后重新发送" }}
          </button>
        </div>
      </div>

      <div class="login-btn" @click="login">登录</div>
    </div>
  </div>
</template>

<script>
import login from '@/api/login'
import { Toast } from 'vant'
export default {
  name: 'LoginIndex',
  async created () {
    this.getCode()
  },
  data () {
    return {
      picUrl: '',
      picKey: '',
      count: 5,
      timerId: null,
      mobile: '13111111111',
      picCode: '',
      code: '246810'
    }
  },
  methods: {
    async getCode () {
      const res = await login.getCode()
      this.picUrl = res.data.base64
      this.picKey = res.data.key
      console.log(res)
    },
    async getload () {
      if (!this.validfn()) {
        return
      }
      if (!this.timerId && this.count === 5) {
        const res = await login.getMsgCode(this.picCode, this.picKey, this.mobile)
        console.log(res)
        this.timerId = setInterval(() => {
          this.count--
          if (this.count <= 0) {
            clearInterval(this.timerId)
            this.count = 5
            this.timerId = null
          }
        }, 1000)
      }
    },
    validfn () {
      if (!/^1[3,9]\d{9}$/.test(this.mobile)) {
        Toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        Toast('请输入正确的验证码')
        return false
      }
      return true
    },
    async login () {
      if (!this.validfn()) {
        return
      }
      const res = await login.getLogin(this.mobile, this.code)
      console.log(res)
      this.$store.commit('user/setUserInfo', res.data)
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  }
}
</script>

<style lang='less' scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
