import request from '@/utils/request'
export default {
  getCode () {
    return request.get('/captcha/image')
  },
  getMsgCode (captchaCode, captchaKey, mobile) {
    return request.post(
      '/captcha/sendSmsCaptcha',
      {
        form: {
          captchaCode,
          captchaKey,
          mobile
        }
      }
    )
  },
  getLogin (mobile, smsCode) {
    return request.post('/passport/login', {
      form: {
        isParty: false,
        mobile,
        partyData: {},
        smsCode
      }
    })
  }
}
